    p.mb-5 A continuacin, se detallan los pasos recomendados para el manejo eficiente de la cama en los galpones:

    .bg--img_03.mb-5
      .row.h-100.align-items-center
        .col-xl
          AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-right")
            .row(titulo="Inspeccin diaria").ajuste-cajaAcordion
              p.mb-3 Revisar la cama dos veces al da (maana y tarde) mediante evaluacin visual y olfativa. Prestar atencin a zonas crticas: debajo de los bebederos, junto a paredes, esquinas y reas de mayor trnsito.
            .row(titulo="Manejo fsico del material").ajuste-cajaAcordion
              p.mb-3 Airear y mantener mecnicamente la cama: voltear diariamente con rastrillo para mantenerla suelta y oxigenada, retirar secciones hmedas o compactadas, y reemplazar con material seco y limpio (viruta sin qumicos, paja picada o cascarilla de arroz).
            .row(titulo="Espesor y calidad recomendados").ajuste-cajaAcordion
              p.mb-3 Espesor uniforme en todo el galpn: mnimo 5 cm y mximo 10 cm. El material debe ser absorbente, sin polvo excesivo ni residuos txicos.
            .row(titulo="Manejo preventivo de humedad").ajuste-cajaAcordion
              p.mb-3 Ajustar la altura y presin de los bebederos para evitar derrames, controlar la ventilacin para mantener la humedad relativa entre 50 % y 70 %, y aplicar cal agrcola u otros absorbentes en zonas hmedas cuando sea necesario.
            .row(titulo="Reemplazo y desinfeccin de la cama").ajuste-cajaAcordion
              p.mb-3 Cambiar totalmente la cama segn su estado o cada 8 - 12 semanas. Retirar la cama usada, lavar y desinfectar el galpn con productos aprobados por el ICA (amonio cuaternario, yodo, glutaraldehdo), y mantener vaco sanitario durante 10 - 15 das antes del reingreso de las aves.
