        h3 Acciones especficas para el manejo diario
        
    p.mb-5 Implementen las siguientes acciones como parte del protocolo diario:

    .row.align-items-center.m-0.mb-5.style___box_6(data-aos="zoom-in")
      .col-xl-auto
