      .col-xl
        <ul class="fa-ul estilo___ul mb-0">
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Revisar la disponibilidad de alimento al inicio de la jornada.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Ajustar la altura de los comederos al nivel del dorso del ave.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Llenar las tolvas hasta un mximo de dos tercios para evitar el desperdicio.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Retirar alimento contaminado o hmedo.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Adaptar la dieta segn la etapa productiva de la parvada.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-check-circle"></i></span>Suplementar con <strong>carbonato de calcio (CaCO)</strong> en la tarde, en cantidades especficas segn la etapa.</li>
        </ul>
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
