        h3 Almacenamiento del concentrado
        
    p.mb-5 La calidad del alimento depende en gran medida de su correcta conservacin. Para ello, se deben seguir estas recomendaciones:

    .bg--img_03.mb-5
      .row.h-100.align-items-center
        .col-xl
          AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-right")
            .row(titulo="Bodegas").ajuste-cajaAcordion
              p.mb-3 Utilizar estibas a una altura mnima de 15 cm del piso y 50 cm de las paredes. Organizar los sacos por lotes y fechas de ingreso siguiendo el sistema PEPS (Primero en Entrar, Primero en Salir). Evitar la humedad, las filtraciones y el contacto directo con el suelo. Implementar control de plagas y roedores mediante sellado de accesos, y restringir el ingreso de aves silvestres.
