            .row(titulo="Recoleccin y materiales").ajuste-cajaAcordion
              p.mb-3 Recolectar los huevos de forma oportuna con materiales limpios y desinfectados que eviten contaminacin o roturas. Utilizar cubetas en buen estado y realizar la recoleccin varias veces al da para prevenir exposicin a humedad, calor o suciedad.
            .row(titulo="Clasificacin por peso").ajuste-cajaAcordion
              p.mb-0 Clasificar los huevos segn su peso para cumplir con los estndares comerciales:
              p.mb-0 Pequeo: menos de 50 g.
              p.mb-0 Mediano: 50 - 60 g.
              p.mb-3 Grande: ms de 60 g.
            .row(titulo="Separacin de huevos defectuosos").ajuste-cajaAcordion
              p.mb-3 Detectar y separar los huevos con defectos visibles (rotos, sucios, fisurados, de doble yema o deformes). Registrar las cantidades por categora y destinarlos al consumo interno controlado o compostaje.
    
    .bg--img_03.mb-5
