            .row(titulo="Limpieza").ajuste-cajaAcordion
              p.mb-3 Aplicar limpieza en seco con esponjillas, papel o lijas finas (lija 1000). Evitar el uso de agua o productos qumicos que puedan daar la cscara o facilitar la contaminacin bacteriana.
            .row(titulo="Categoras de clasificacin").ajuste-cajaAcordion
              p.mb-3 Conservar los huevos en bandejas limpias, colocados con la parte puntiaguda hacia abajo, en un cuarto fresco, limpio y ventilado. Mantener temperatura entre 15 y 20 C y humedad relativa del 70 - 80 %. Evitar exposicin solar directa y aplicar el sistema PEPS (Primero en Entrar, Primero en Salir).
            .row(titulo="Registros diarios").ajuste-cajaAcordion
              p.mb-3 Documentar diariamente la cantidad de huevos recolectados, su clasificacin y los defectos observados. Esto permite controlar la produccin, detectar problemas sanitarios y evaluar el desempeo del lote. Se recomienda una tabla con las siguientes columnas: Fecha, Total de huevos recolectados, Clasificacin (Peq / Med / Gde), Huevos defectuosos y Observaciones.
