    p.mb-5 En el siguiente video se explica la funcin del alojamiento en la produccin avcola, destacando su papel en la proteccin y el confort de las aves. Tambin se describen las orientaciones, dimensiones y condiciones tcnicas que deben cumplir los galpones para favorecer la productividad y el bienestar animal segn el clima y el tipo de produccin.

