    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Procedimiento

    p.mb-5 A continuacin, se detallan el procedimiento recomendado para la recepcin de aves, abarcando las fases antes, durante y despus de su llegada al galpn.

    TabsC.color-acento-contenido(data-aos="zoom-in").mb-5
      .py-3.py-md-4(titulo="Previo a la llegada")
        .row.justify-content-center
          .col-xl-5.order-2.order-lg-1
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img152.png', alt="" ).m-auto
          .col-xl-7.order-1.order-lg-2.mb-4.mb-lg-0
            h4.txt--purpura.mb-4 Previo a la llegada
            p.mb-0 Preparar rigurosamente el entorno antes del arribo. Asegurar que el galpn est desinfectado, con cama nueva, seca y libre de contaminantes. Desinfectar equipos (bebederos, tolvas, calefactores, bsculas, ventiladores, etc.) y comprobar su funcionamiento. Instalar cesteaderos o perchas segn la densidad del lote. Activar pediluvios en todos los accesos y permitir el ingreso solo al personal autorizado con indumentaria completa de bioseguridad (overol, mascarilla, guantes y botas plsticas). Exigir lavado de manos con agua y jabn antes de ingresar.

      .py-3.py-md-4(titulo="Durante la recepcin")
        .row.justify-content-center
          .col-xl-5.order-2.order-lg-1
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img153.png', alt="" ).m-auto
          .col-xl-7.order-1.order-lg-2.mb-4.mb-lg-0
            h4.txt--purpura.mb-4 Durante la recepcin
            p.mb-0 Cumplir medidas estrictas para garantizar la integridad de las aves. Transportarlas nicamente despus de la semana 16 de vida, segn la normatividad vigente. Descargar en horarios frescos (5:00 - 7:00 a. m. o 4:00 - 6:00 p. m.), manipular con cuidado, evitando golpes o amontonamientos. Contar las aves, revisar su estado sanitario y descartar las lesionadas o dbiles. Trasladarlas al galpn con acceso inmediato a agua potable tratada o clorada.
      
      .py-3.py-md-4(titulo="Despus de la recepcin")
        .row.justify-content-center
          .col-xl-5.order-2.order-lg-1
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img154.png', alt="" ).m-auto
          .col-xl-7.order-1.order-lg-2.mb-4.mb-lg-0
            h4.txt--purpura.mb-4 Despus de la recepcin
            p.mb-0 Evaluar el estado general del lote. Vigilar el comportamiento durante la primera hora (jadeo, estrs, agrupamiento, inactividad). Registrar mortalidad inicial y cantidad exacta de aves recibidas. Pesar una muestra del 10 % y comparar con los estndares de la lnea gentica. Suministrar agua con electrolitos y antiestrs durante los primeros tres das, lavando los bebederos a diario. Iniciar la alimentacin con concentrado una hora despus de la llegada y ajustar la dieta segn la tabla nutricional y la normativa vigente. Establecer horarios fijos de alimentacin para minimizar el estrs.
    
